
'use client';

import { useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import type { ContactMessage } from '@/lib/messages';
import { format } from 'date-fns';
import { supabase } from '@/lib/supabase';
import { useToast } from '@/hooks/use-toast';
import { Loader2 } from 'lucide-react';

export default function MessagesPage() {
  const [messages, setMessages] = useState<ContactMessage[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedMessage, setSelectedMessage] = useState<ContactMessage | null>(null);
  const { toast } = useToast();

  useEffect(() => {
    const fetchMessages = async () => {
      setLoading(true);
      const { data, error } = await supabase.from('messages').select('*').order('date', { ascending: false });
      if (error) {
        toast({
          title: "Error fetching messages",
          description: error.message,
          variant: "destructive",
        });
      } else if (data) {
        setMessages(data);
        if (data.length > 0) {
            setSelectedMessage(data[0]);
        }
      }
      setLoading(false);
    };

    fetchMessages();
  }, [toast]);

  return (
    <div className="grid gap-4 md:grid-cols-[1fr_250px] lg:grid-cols-[1fr_350px]">
      <div className="flex flex-col gap-4">
        <div className="flex items-center">
            <h1 className="text-lg font-semibold md:text-2xl">Inbox</h1>
        </div>
        <Card>
           <CardHeader>
             <CardTitle>Customer Messages</CardTitle>
             <CardDescription>
                Review and respond to inquiries from the contact form.
            </CardDescription>
           </CardHeader>
          <CardContent>
            {loading ? (
                 <div className="flex justify-center items-center h-48">
                    <Loader2 className="h-8 w-8 animate-spin" />
                </div>
            ) : (
                <Table>
                <TableHeader>
                    <TableRow>
                    <TableHead>From</TableHead>
                    <TableHead>Service</TableHead>
                    <TableHead className="text-right">Date</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {messages.map((message) => (
                    <TableRow
                        key={message.id}
                        className={`cursor-pointer ${
                        selectedMessage?.id === message.id ? 'bg-muted/50' : ''
                        }`}
                        onClick={() => setSelectedMessage(message)}
                    >
                        <TableCell className="font-medium">{message.name}</TableCell>
                        <TableCell>
                        <Badge variant="outline">{message.service}</Badge>
                        </TableCell>
                        <TableCell className="text-right">
                        {format(new Date(message.date), 'MMM dd, yyyy')}
                        </TableCell>
                    </TableRow>
                    ))}
                </TableBody>
                </Table>
            )}
          </CardContent>
        </Card>
      </div>

      <div className="flex flex-col gap-4">
        <h1 className="text-lg font-semibold md:text-2xl invisible">Details</h1>
         {selectedMessage ? (
          <Card>
            <CardHeader>
              <CardTitle>{selectedMessage.name}</CardTitle>
              <CardDescription>
                <a href={`mailto:${selectedMessage.email}`} className="hover:underline">
                  {selectedMessage.email}
                </a>
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Service Inquiry</p>
                  <p>{selectedMessage.service}</p>
                </div>
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Message</p>
                  <p className="text-sm leading-relaxed">{selectedMessage.message}</p>
                </div>
                 <div>
                  <p className="text-sm font-medium text-muted-foreground">Received</p>
                  <p className="text-sm">{format(new Date(selectedMessage.date), 'PPPP')}</p>
                </div>
              </div>
            </CardContent>
          </Card>
        ) : (
          !loading && <div className="flex h-full items-center justify-center rounded-lg border border-dashed bg-card p-4">
            <p className="text-muted-foreground">Select a message to view its details</p>
          </div>
        )}
      </div>
    </div>
  );
}
